.brand img {
    width: 100%;
    height: auto;
    }

.brand {
    text-align: center;
}

@media only screen and (max-width: 600px) {
    .brand {
        max-width: 60%;        
    }
  }

/*Nav/heading grid */
.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #fff;
}

/* "learn the basics" text */
.main-text {
  grid-column-start: 1;
  grid-column-end: 3;
  text-align: center;
  font-size: 3em;
  background-color: #f8f9fa;
}



/* If the screen size is 601px wide or more, set the font-size of <div> to 80px */
@media screen and (min-width: 601px) {
.main-text {
  font-size: 2.2em;
}
}
/* If Screen size is less than 770px, do this*/
@media screen and (min-width: 770px) {
.main-text {
  font-size: 3em;
}
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 1.5em */
@media screen and (max-width: 600px) {
.main-text {
  font-size: 1.4em;
}
}

/*setting the body to grid, and creating a 2 column layout*/
.main-body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 1em;
  grid-row-gap: 1em;
  margin: 0 10% 1% 10%;
  font-size: 1.2em;
}

/*Display block on mobile devices smaller than 770px and small margins margins*/
@media only screen and (max-width: 770px) {
.main-body {
  display: block;
  margin: 0 4px 0 4px;
  font-size: 1em;
  padding-bottom: 10px;
}
}

/*For tablet users, makes smaller margins creates  */
@media only screen and (max-width: 1024px) {
.main-body {    
  margin: 0 4px 0 4px;
  font-size: 1.1em;
}
}

/* Learn web development text at top of page */
.body-title {
  text-align: center;
  grid-column: 1 / span 2;
  font-size: 1.6em;
}

/* change font size from 1.6 to 1.3 em on screens smaller than 600px*/
@media screen and (max-width: 600px) {
  .body-title {
    font-size: 1.3em;
  }
}

/* html image div*/
.imgs {
  grid-column-start: 1fr;
  grid-column-end: 2fr;
  }

/*Body images */
.body-img{
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width: 770px) {
.body-img {
  box-shadow: none;
  padding-bottom: 10px;
}
}

/*html image*/
img {
width: 100%;
}

/*Text is in right column on screen*/
.body-text {
grid-column-start: 2fr;
grid-column-end: 3fr;
}

@media screen and (max-width: 770px) {
.body-text {
  padding-bottom: 2px;
}
}

.example {
font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
-webkit-font-feature-settings: "c2sc";
        font-feature-settings: "c2sc";
font-variant: small-caps;
}

/*hr styles for top and bottom of page*/
.hr {
      grid-column-start: 1;
      grid-column-end: 3;
      border: 0;
      height: 1px;
      background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
      background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
      margin-bottom: 5px;
  }

/*Display footer as grid in a 3 column layout, centers content*/
.main-footer {
display: grid;
grid-template-columns: 1fr 1fr 1fr;
-webkit-justify-content: center;
        justify-content: center;
background-color: #f8f9fa;
height: 10vh;
text-align: center;
}

/*icon size in footer*/
.footer-icon {
width: 3rem;
height: 3rem;
}

/*Reduce icone size when screen is smaller than 1024px*/
@media only screen and (max-width: 1024px) {
.footer-icon {
  width: 2.5rem;
height: 2.5rem;
}
}

.button {
background-color: red;
color: white;
border: 2px solid #555555;
}

.button:hover {
background-color: #555555;
color: white;
}

@media only screen and (max-width: 770px) {
  #scroll-link {
   display: block;
  }
  }

  .footer-icon:hover {
    opacity: .7;
  }

